import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Task } from "./../modules/task";
import { Observable } from 'rxjs';

const httpOptions = {
  headers: new HttpHeaders ({
    'Content-Type': 'application/json; charset=UTF-8',
  })
}

@Injectable({
  providedIn: "root"
})
export class TaskService {
   url="https://my-json-server.typicode.com/lytab/JsonDB/tasks";
  constructor(private http: HttpClient) {}
  getTasks() {
    return this.http.get<Task[]>(`${this.url}`);
  }
  delete(id):Observable<Task>{
    return this.http.delete<Task>(`${this.url}/${id}`);
  }
  persist(task:Task):Observable<Task>{
return this.http.post<Task>(this.url,task,httpOptions);
  }
  completed(id,completed){
return this.http.patch(`${this.url}/${id}`,{completed:!completed});
  }
update(task){
  return this.http.put<Task>(`${this.url}/${task.id}`,{label:task.label,completed:task.completed});
}
}
